@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;800&display=swap");
*,
*::after,
*::before {
  box-sizing: inherit;
}

html {
  height: 100%;
  box-sizing: border-box;
}

body {
  position: relative;
  margin: 0;
  display: grid;
  min-height: 100%;
  grid-template-rows: min-content 1fr min-content;
  font-family: "Montserrat", "Arial", sans-serif;
  font-weight: 300;
  line-height: 1.25;
  background: #020103;
  color: #ffffff;
  align-content: start;
}

.open-menu {
  overflow: hidden;
}
.open-menu::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.9);
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  max-width: 100%;
  margin: 0 auto;
  padding: 0 2.9rem;
}
@media screen and (max-width: 768px) {
  .container {
    padding: 0 1.8rem;
  }
}
@media screen and (max-width: 520px) {
  .container {
    padding: 0 1.2rem;
  }
}

.header {
  position: absolute;
  z-index: 10;
  width: 100%;
  padding: 2.5rem 0;
  background: rgba(0, 0, 0, 0.2);
  animation: animationHeader 0.6s ease-in;
  overflow: hidden;
}

.header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media screen and (max-width: 1550px) {
  .header__container {
    gap: 2rem;
  }
}

@media screen and (max-width: 1300px) {
  .header__logo img {
    width: 10rem;
  }
}
@media screen and (max-width: 1080px) {
  .header__logo img {
    width: 8.5rem;
  }
}

@keyframes animationHeader {
  0% {
    transform: translateY(-100%);
  }
  100% {
    transform: translateY(0);
  }
}
.main-nav {
  width: 75%;
}
@media screen and (max-width: 1550px) {
  .main-nav {
    width: auto;
  }
}
.main-nav--opened .main-nav__container {
  left: 0;
}
.main-nav--opened .main-nav__button-open span:nth-child(1) {
  top: calc(50% - 1px);
  transform: rotate(45deg);
}
.main-nav--opened .main-nav__button-open span:nth-child(2) {
  bottom: -10px;
}
.main-nav--opened .main-nav__button-open span:nth-child(3) {
  bottom: calc(50% - 1px);
  transform: rotate(-45deg);
}
@media screen and (max-width: 1024px) {
  .main-nav--no-js .main-nav__container {
    display: flex;
    width: 100%;
    bottom: -2rem;
    left: 0;
    justify-content: center;
    position: absolute;
  }
  .main-nav--no-js .main-nav__button {
    display: none;
  }
  .main-nav--no-js .main-nav__link {
    font-size: 1.2rem;
    padding-left: 1rem;
    padding-right: 1rem;
  }
  .main-nav--no-js .main-nav__item::after {
    width: 0.3rem;
    height: 0.3rem;
    background: #ffffff;
    border-radius: 0.06rem;
  }
}

.main-nav__button-open {
  position: relative;
  overflow: hidden;
  display: none;
  width: 3rem;
  height: 2rem;
  background: 0 0;
  border: 0;
}
.main-nav__button-open span {
  position: absolute;
  left: 0;
  right: 0;
  background: #ffffff;
  height: 2px;
  transition: 0.4s top ease-in, 0.4s bottom ease-in, 0.4s transform ease-in;
}
.main-nav__button-open span:nth-child(1) {
  top: 0;
}
.main-nav__button-open span:nth-child(2) {
  bottom: calc(50% - 1px);
}
.main-nav__button-open span:nth-child(3) {
  bottom: 0;
}
@media screen and (max-width: 1024px) {
  .main-nav__button-open {
    display: block;
  }
}

.main-nav__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 3rem;
}
@media screen and (max-width: 1080px) {
  .main-nav__container {
    gap: 2rem;
  }
}
@media screen and (max-width: 1024px) {
  .main-nav__container {
    position: fixed;
    height: 100vh;
    width: 100vw;
    top: 0;
    left: 110vw;
    background: rgba(0, 0, 0, 0.9);
    flex-direction: column;
    justify-content: center;
    gap: 5rem;
    transition: 0.4s left ease-in;
  }
}

.main-nav__list {
  margin: -2.2rem;
  padding: 0;
  list-style-type: none;
  flex-wrap: wrap;
  display: flex;
}
@media screen and (max-width: 1024px) {
  .main-nav__list {
    flex-direction: column;
    align-items: center;
  }
}

.main-nav__item {
  display: flex;
  align-items: center;
}
.main-nav__item:not(:last-child)::after {
  content: "";
  width: 0.63rem;
  height: 0.63rem;
  background: #ffffff;
  border-radius: 0.13rem;
  transform: translateY(0.15rem) rotate(-45deg);
}
@media screen and (max-width: 1024px) {
  .main-nav__item {
    flex-direction: column;
  }
}

.main-nav__link {
  display: inline-block;
  padding: 2.2rem;
  font-size: 2rem;
  line-height: 1;
  font-weight: 400;
  text-decoration: none;
  cursor: pointer;
  color: inherit;
  transition: 0.3s transform ease-in;
}
.main-nav__link:hover {
  transform: scale(0.9);
}
@media screen and (max-width: 1550px) {
  .main-nav__link {
    font-size: 1.8rem;
  }
}
@media screen and (max-width: 1390px) {
  .main-nav__link {
    font-size: 1.6rem;
  }
}
@media screen and (max-width: 1240px) {
  .main-nav__link {
    padding: 1.8rem;
    font-size: 1.5rem;
  }
}
@media screen and (max-width: 1140px) {
  .main-nav__link {
    font-size: 1.3rem;
  }
}
@media screen and (max-width: 1024px) {
  .main-nav__link {
    font-size: 1.6rem;
  }
}

.main-nav__button {
  display: inline-block;
  padding: 1.25rem 1.9rem;
  font-size: 2rem;
  font-weight: 600;
  line-height: normal;
  color: inherit;
  text-decoration: none;
  background: #0085ff;
  border: 0;
  cursor: pointer;
  border-radius: 0.6rem;
  box-shadow: 0 6px 0 0 #014d93;
  transition: 0.3s transform ease-in;
}
.main-nav__button:hover {
  transform: scale(0.95);
}
@media screen and (max-width: 1550px) {
  .main-nav__button {
    font-size: 1.8rem;
  }
}
@media screen and (max-width: 1390px) {
  .main-nav__button {
    font-size: 1.6rem;
  }
}
@media screen and (max-width: 1240px) {
  .main-nav__button {
    font-size: 1.5rem;
  }
}
@media screen and (max-width: 1140px) {
  .main-nav__button {
    font-size: 1.3rem;
    padding: 1.1rem 1.6rem;
  }
}
@media screen and (max-width: 1024px) {
  .main-nav__button {
    font-size: 1.5rem;
    padding: 1.25rem 1.9rem;
  }
}

.promo {
  overflow: hidden;
  position: relative;
  background: url("../img/promo-bg.jpg");
  background-size: auto 100%;
}
.promo::before {
  content: "";
  width: 100%;
  height: 12rem;
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: 1;
  background: linear-gradient(0deg, rgb(2, 1, 3) 0%, rgba(255, 255, 255, 0) 100%);
}
@media (min-resolution: 2dppx) {
  .promo {
    background-image: url("../img/promo-bg@2x.jpg");
  }
}

.promo__container {
  padding: 14rem 0 17rem;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
