  font-size: 4rem;
  font-weight: 600;
  text-shadow: 0 4px 9px rgba(0, 0, 0, 0.8);
  animation: animationApperance 0.6s ease-in;
}
.promo__heading span {
  animation: animationApperance 0.6s ease-in forwards;
  transform: scale(0);
  margin: -1.6rem 0 0;
  display: block;
  text-shadow: 0 4px 13px rgba(0, 0, 0, 0.7);
  font-size: 8rem;
  font-weight: 800;
  color: #0085ff;
}
