  .promo__heading span {
    font-size: 7rem;
  }
}
@media screen and (max-width: 1024px) {
  .promo__heading span {
    margin-top: -1rem;
    font-size: 6rem;
  }
}
@media screen and (max-width: 750px) {
  .promo__heading span {
    font-size: 5rem;
  }
}
@media screen and (max-width: 670px) {
  .promo__heading span {
    margin-top: 0;
    font-size: 4rem;
  }
}
@media screen and (max-width: 430px) {
  .promo__heading span {
    font-size: 3rem;
  }
}
@media screen and (max-width: 1110px) {
  .promo__heading {
    margin-bottom: 4rem;
    font-size: 3.5rem;
  }
}
@media screen and (max-width: 1024px) {
  .promo__heading {
    margin-bottom: 3rem;
    font-size: 3rem;
  }
}
@media screen and (max-width: 750px) {
  .promo__heading {
    font-size: 2rem;
  }
}
@media screen and (max-width: 750px) {
  .promo__heading {
    font-size: 1.7rem;
  }
}
@media screen and (max-width: 430px) {
