    font-size: 1.4rem;
  }
}

.promo__button {
  position: relative;
  padding: 1.3rem 2.1rem;
  font-size: 2.3rem;
  font-weight: 600;
  line-height: normal;
  color: inherit;
  text-decoration: none;
  background: #0085ff;
  border-radius: 0.6rem;
  box-shadow: 0 6px 0 0 #014d93;
