  }
}
.promo__people-bg--2 {
  right: 0;
  background-image: url("../img/promo-people-bg-2.png");
}
@media (min-resolution: 2dppx) {
  .promo__people-bg--2 {
    background-image: url("../img/promo-people-bg-2@2x.png");
  }
}
@media screen and (max-width: 1430px) {
  .promo__people-bg--2 {
    right: -5rem;
  }
}
@media screen and (max-width: 870px) {
  .promo__people-bg--2 {
    right: -7rem;
  }
}
@media screen and (max-width: 520px) {
