  }
}

@keyframes animationApperance {
  0% {
    transform: scale(0);
  }
  100% {
    transform: scale(1);
  }
}
@keyframes animationPeopleLeft {
  0% {
    left: -100%;
  }
  70% {
    transform: translateY(30%) rotate(0);
  }
  100% {
    left: 0;
    transform: translateY(30%) rotate(8.5deg);
  }
}
@keyframes animationPeopleRight {
  0% {
    right: -100%;
  }
  70% {
    transform: translateY(30%) rotate(0);
  }
  100% {
    right: 0;
    transform: translateY(30%) rotate(-6.5deg);
  }
}
@keyframes pulse {
  0% {
    box-shadow: 0 0 0 0 rgba(1, 77, 147, 0.4), 0 0 0 2px #014d93, 0 0 0 4px rgba(1, 77, 147, 0.9);
  }
  50% {
    box-shadow: 0 0 0 8px rgba(1, 77, 147, 0.8), 0 0 0 15px rgba(1, 77, 147, 0.6), 0 0 0 20px rgba(1, 77, 147, 0.3);
  }
  100% {
    box-shadow: 0 0 0 0 rgba(1, 77, 147, 0.4), 0 0 0 2px rgba(1, 77, 147, 0.2), 0 0 0 4px rgba(1, 77, 147, 0.1);
  }
}
